<template>
  <section class="checkFeeds pt-2 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card class="colwidth">
      <v-card-title>
        <v-layout align-start
                  justify-space-between
                  row
                  fill-height>
          <v-flex xs6
                  class="pr-3">
            <v-text-field clearable
                          label="Find Users..."
                          v-model="lookup"
                          single-line
                          required
                          @input="$v.lookup.$touch()"
                          @blur="$v.lookup.$touch()"
                          @keyup.enter="GetUsers()"
                          :error-messages="nameErrors">
            </v-text-field>
          </v-flex>
          <v-flex xs3>
            <v-select v-model="select"
                      :items="items"
                      :error-messages="selectErrors"
                      label="Domain"
                      required
                      @change="$v.select.$touch()"
                      @blur="$v.select.$touch()">
            </v-select>
          </v-flex>
          <v-flex xs12>
            <div class="pl-5">
              <v-progress-circular indeterminate
                                   color="primary"
                                   class="mt-3 ml-0"
                                   v-if="loading">
              </v-progress-circular>

              <v-btn :disabled="this.$v.$invalid"
                     v-if="!loading"
                     color="tfgprimary"
                     class="mt-3 ml-0"
                     round
                     small
                     :dark="!this.$v.$invalid"
                     @click="GetUsers()">
                Find
              </v-btn>
              <exportMenu v-if="adUsers.length > 0"
                          :data="adUsers"
                          :headers="headersEdited"></exportMenu>

            </div>
          </v-flex>
          <v-flex xs12>
            <v-text-field v-model="search"
                          prepend-icon="filter_list"
                          label="Filter results"
                          single-line
                          hide-details>
            </v-text-field>
          </v-flex>
        </v-layout>
      </v-card-title>

      <v-data-table :headers="headers"
                    :items="adUsers"
                    :loading=false
                    item-key="id"
                    :search="search"
                    :rows-per-page-items="rowsperpage"
                    class="px-2">

        <v-progress-linear slot="progress"
                           color="primary"
                           indeterminate
                           height="5">
        </v-progress-linear>

        <template slot="items"
                  slot-scope="props">
          <tr>
            <td class="text-xs-left pl-3">{{ props.item.name }}</td>
            <td class="text-xs-left pl-3">{{ props.item.samAccountName }}</td>
            <td class="text-xs-left pl-3">{{ props.item.email }}</td>
            <td class="text-xs-left pl-3">{{ props.item.phone }}</td>
            <td class="text-xs-center px-1">{{ props.item.enabled }}</td>
            <td class="text-xs-center px-1">{{ props.item.passwordNeverExpires }}</td>
            <td class="text-xs-left pl-3 colwidth-250">{{ props.item.distinguishedName }}</td>
            <td class="text-xs-center px-1">
              <MemberOf :data="props.item.samAccountName"
                        :domain="select"></MemberOf>
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-card>
  </section>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";
import { FindADUser } from "@/services/api/ActiveDirectory";
import { Notify } from "@/services/HelperService";
import exportMenu from "@/components/exportMenu";
import PageIntro from "@/components/PageIntro.vue";
import MemberOf from "@/components/ActiveDirectory/MemberOf";
import { DataTableMixin } from "@/components/Mixins/DataTableMixin";
import { AdVuelidateMixin } from "@/components/Mixins/AdVuelidateMixin";

export default {
  mixins: [validationMixin, DataTableMixin, AdVuelidateMixin],

  validations: {
    lookup: {
      required,
      minLength: minLength(4)
    },
    select: {
      required
    }
  },

  components: {
    exportMenu,
    MemberOf,
    PageIntro
  },

  data() {
    return {
      introName: "Active Directory Find Users and Groups",
      adUsers: [],
      lookup: "",
      select: "HO",
      search: "",
      items: ["HO", "STORES", "TFGSTORES.local"],
      headers: [
        {
          text: "Name",
          align: "Left",
          sortable: true,
          value: "name",
          class: "pl-0 ml-0"
        },

        {
          text: "Logon Name",
          align: "center",
          sortable: true,
          value: "samAccountName",
          class: "pl-1"
        },
        {
          text: "Email",
          align: "center",
          sortable: true,
          value: "email",
          class: "pl-1"
        },
        {
          text: "Phone",
          align: "center",
          sortable: true,
          value: "phone",
          class: "mx-0 px-0"
        },
        {
          text: "Enabled",
          align: "center",
          sortable: true,
          value: "enabled",
          class: "mx-0 pr-2 pl-0"
        },
        {
          text: "PassNeverExpires",
          align: "center",
          sortable: true,
          value: "passwordNeverExpires",
          class: "mx-0 pr-2 pl-0"
        },
        {
          text: "DistinguishedName",
          align: "center",
          class: "pl-1",
          value: "distinguishedName"
        },
        {
          text: "MemberOf",
          align: "center",
          sortable: true,
          value: "View",
          class: "mr-1"
        }
      ]
    };
  },

  computed: {
    headersEdited() {
      let customheaders = this.headers.slice();
      customheaders.pop();
      return customheaders;
    }
  },

  methods: {
    GetUsers() {
      this.loading = true;
      FindADUser({ UserGroup: this.lookup, Domain: this.select })
        .then(data => {
          this.adUsers = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>
